import React, { Component } from 'react';
import { connect } from 'react-redux';

import { bindActionCreators } from 'redux';

import {GridList, GridTile} from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import RemoveImage from 'material-ui/svg-icons/content/clear';

import  { removeTapeImage } from '../actions/removeTapeImage';

const styles = {
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
    },
    gridList: {
        display: 'flex',
        flexWrap: 'nowrap',
        overflowX: 'auto',
    },
    titleStyle: {
        color: 'rgb(0, 188, 212)',
    },
};

const iconStyles = {
    marginRight: 24,
};

class ImageTape extends Component {
    componentWillReceiveProps(nextProps) {
        // console.log('nextProps', nextProps.imageTape.imagesList)
    }

    _onClickRemoveImage(image) {
        this.props.removeTapeImage({
            imagesList: this.props.imageTape.imagesList,
            image: image
        });
    }

    renderList() {
        return this.props.imageTape.imagesList.map((image) => {
            return (
                <GridTile
                    key={image.key}
                    title={image.imageName}
                    actionIcon={<IconButton>
                        <RemoveImage
                            style={iconStyles} color="rgb(255, 0, 0)"
                            onClick={event => this._onClickRemoveImage(image)}
                        />
                    </IconButton>}
                    titleStyle={styles.titleStyle}
                    titleBackground="linear-gradient(to top, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0.3) 70%,rgba(0,0,0,0) 100%)"
                >
                    <img src={image.src} />
                </GridTile>
            )
        });
    }

    render() {
        if (this.props.imageTape.imagesList.length < 1) {
            return <div>
                No image selected
            </div>
        }

        return (
            <div style={styles.root}>
                <GridList style={styles.gridList} cols={1}>
                    {this.renderList()}
                </GridList>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        imageTape: state.imageTape
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        removeTapeImage: removeTapeImage
    }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(ImageTape)


