const initialImagesListState = {
    imagesList:[]
};

function updateImagesList(imagesList, image) {
    imagesList.forEach(function (item, i, arr) {

       if(item.key == image.key) {
        console.log(item.key, image.key, i)
           arr.splice(i, 1)
       }
    });
}

export default function (state = initialImagesListState, action) {
    switch (action.type) {
        case 'ADD_IMAGE':

        return {
            ...state,
            imagesList: [...state.imagesList, action.payload]
        };

        case 'IMAGE_DELETE':
            // console.log(action.payload);
            updateImagesList(action.payload.imagesList, action.payload.image);

        return {
            ...state
            // imagesList: [...state.imagesList.splice(updateImagesList(action.payload.imagesList, action.payload.image), 1)]
        }
    }

    return state
}


