const initialDefaults = {
    value: '',
    hintText: 'paste URL name'
}

export default function (state = initialDefaults, action) {
    switch (action.type) {
        case 'SET_DEFAULTS':
            state = initialDefaults;

            // console.log('SET_DEFAULTS', state, 'initialDefaults', initialDefaults, 'action', action);

            return state;

        case 'SET_INPUT_DATA':
            state = action.payload;

            // console.log('SET_INPUT_DATA', state, 'initialDefaults', initialDefaults, 'action', action);

            return state
    }

    return state
}


