import React from 'react';
import Paper from 'material-ui/Paper';

const style = {
    height: 20,
    width: '70%',
    margin: 20,
    textAlign: 'center',
    display: 'inline-block',
};

const PaperExampleSimple = () => (
    <div>
        <Paper style={style} zDepth={3}>v.0.1</Paper>
    </div>
);

export default PaperExampleSimple;


